import { useEffect, useRef, useState } from "react";

import { IApiResponse, IFetchParams, IFetchStatus } from "../intefaces";
import { defaultResponse, FetchHeader } from "../store";

const useFetch = (fetchParams: IFetchParams): IFetchStatus => {
  const { url, debugMode, request, isSingleRequest } = fetchParams;

  const [response, setResponse] = useState<IApiResponse>(defaultResponse);

  const [isLoading, setIsLoading] = useState<boolean>(false);

  const isError = useRef(false);

  const [isRequestSent, setIsRequestSent] = useState<boolean>(false);

  const [pages, setPages] = useState<string>("1");

  const isFirstRender = useRef(true);

  useEffect(() => {
    const abortCont = new AbortController();

    const connectToApi = async () => {
      setIsRequestSent(false);

      if (debugMode.current) {
        return;
      }

      if (isFirstRender.current && !isSingleRequest?.current) {
        isFirstRender.current = false;

        return;
      }

      if (isError.current) {
        isError.current = false;
      }

      setIsLoading(true);

      setIsRequestSent(true);

      try {
        const rawResponse = await fetch(url.current, { headers: FetchHeader, signal: abortCont.signal });

        if (!rawResponse.ok) {
          throw Error("invalid request");
        }

        const newResponse: IApiResponse = await rawResponse.json();

        setIsLoading(false);

        setPages(String(newResponse.pages));

        setResponse(newResponse);
      } catch (err) {
        if (err.name !== "AbortError") {
          isError.current = true;

          setPages("1");

          setIsLoading(false);
        }
      }
    };

    connectToApi();
    return () => abortCont.abort();
  }, [request, debugMode, isSingleRequest, url]);

  const isErrorCurr = isError.current;

  return { response, isError: isErrorCurr, isLoading, isRequestSent, pages };
};

export default useFetch;
