import React from "react";

import { IInputProps } from "../../../../../intefaces";
import { nextButton, prevButton } from "../../../../../store";

import Button from "./Button";

const NavButtons: React.FC<IInputProps> = props => {
  const { saveInput, value, pages } = props;

  const pagesNum = +(pages as string);

  const pageNum = +value.page;

  return (
    <div className="nav-buttons">
      <h2>
        {pageNum < pagesNum ? pageNum : pagesNum}/{pages}
      </h2>
      <Button
        buttonDataBundle={prevButton}
        page={pageNum}
        saveInput={saveInput}
        isDisabled={pageNum === 1 || pagesNum === 1}
      />
      <Button buttonDataBundle={nextButton} page={pageNum} saveInput={saveInput} isDisabled={pageNum >= pagesNum} />
    </div>
  );
};

export default NavButtons;
