import React, { useRef, useState } from "react";

import { IRequest } from "../../../intefaces";
import { sortBy, defaultRequest, order, select } from "../../../store";

import CardsContainer from "../Cards/CardsContatiner";
import InputRadioWrap from "./components/Radio/InputRadioWrap";
import InputSearch from "./components/InputSearch";
import Select from "./components/Select/Select";
import NavButtons from "./components/NavButtons/NavButtons";

import useFetch from "../../../Logic/useFetch";
import createlUrlFetchAll from "../../../Logic/createUrlFetchAll";

const SearchBar: React.FC = () => {
  const [request, setRequest] = useState<IRequest>({ ...defaultRequest });

  const [searchText, setSearchText] = useState<string>("");

  const debugMode = useRef(false);

  const urlString = createlUrlFetchAll(request);

  const url = useRef(urlString);

  const { response, isError, isLoading, isRequestSent, pages } = useFetch({ request, debugMode, url });

  const wrapClasses = isLoading ? "search inactive" : "search";

  const checkRequest = () => {
    if (isLoading) {
      return;
    }

    if (searchText !== request.content) {
      debugMode.current = true;

      setRequest(prev => ({ ...prev, page: "1" }));

      debugMode.current = false;
    }

    setRequest(prev => ({ ...prev, content: searchText }));
  };

  const changeRequest = (text: string, type: string) => {
    debugMode.current = true;

    checkRequest();

    debugMode.current = false;

    setRequest(prev => ({ ...prev, [type]: text }));
  };

  const setSearchLine = (value: string) => {
    setSearchText(value);
  };

  return (
    <section className={wrapClasses}>
      <h2 className="headline search__headline">Find any LOTR character</h2>
      <InputSearch value={searchText} saveInput={setSearchLine} submitRequest={checkRequest} />
      <InputRadioWrap value={request} saveInput={changeRequest} innerText={sortBy} />
      <InputRadioWrap value={request} saveInput={changeRequest} innerText={order} />
      <Select value={request} saveInput={changeRequest} innerText={select} />
      {!isLoading && !isError && <CardsContainer results={response.docs} />}
      {!isLoading && !isError && isRequestSent && !response.docs.length && <p>Sorry, no results for your query</p>}
      {isLoading && <div className="hourglass" />}
      {isError && <p className="error-msg">Sorry, server hasn&apos;t answered. Try again later. </p>}
      <NavButtons value={request} saveInput={changeRequest} pages={pages} />
    </section>
  );
};

export default SearchBar;
