import React from "react";

import { IInputProps, IRadio, IInnerText } from "../../../../../intefaces";

import InputRadio from "./InputRadio";

const InputRadioWrap: React.FC<IInputProps> = props => {
  const { innerText } = props;

  const { name, labels, headline } = innerText as IInnerText;

  const radioLabels = labels as string[];

  return (
    <div className="form__input-wrap">
      <p className="form__input-label">{headline}</p>
      <div className="form__input-radio-wrap">
        {radioLabels.map(label => {
          const labelData: IRadio = {
            labelText: label,
            name,
          };
          return <InputRadio radioData={labelData} callback={props} key={labelData.labelText} />;
        })}
      </div>
    </div>
  );
};

export default InputRadioWrap;
