import React from "react";

import { ISearchProps } from "../../../../intefaces";

const InputSearch: React.FC<ISearchProps> = props => {
  const name = "content";

  const { value, isErrors, submitRequest } = props;

  const setInput = (event: React.ChangeEvent<HTMLInputElement>) => {
    props.saveInput(event.target.value);
  };

  const checkKey = (event: React.KeyboardEvent) => {
    if (submitRequest && event.key === "Enter") {
      submitRequest();
    }
  };

  return (
    <div className="form__input-wrap">
      <input
        onChange={setInput}
        onKeyPress={checkKey}
        value={value}
        name={name}
        className="form__input"
        type="search"
        id="input-text"
      />

      {isErrors && <p className="form__error-msg">Field can`t be empty</p>}
    </div>
  );
};

export default InputSearch;
