import React from "react";

import { IButtonDataBundle } from "../../../../../intefaces";

const Button: React.FC<IButtonDataBundle> = props => {
  const { buttonDataBundle, page, saveInput, isDisabled } = props;

  const { name, buttonData } = buttonDataBundle;

  const { classes, innerText } = buttonData;

  const wrapClasses = isDisabled ? "form__submit-wrap inactive" : "form__submit-wrap";

  const changePage = () => {
    const newPage = innerText === "Next" ? page + 1 : page - 1;

    saveInput(String(newPage), name);
  };
  return (
    <div className={wrapClasses}>
      <button name={name} className={classes} onClick={changePage} type="button">
        {innerText}
      </button>
    </div>
  );
};

export default Button;
