import React, { useRef, useState } from "react";
import { useParams } from "react-router-dom";

import { IRequest } from "../../../intefaces";
import { defaultRequest } from "../../../store";

import createlUrlFetch from "../../../Logic/createUrlFetch";
import useFetch from "../../../Logic/useFetch";

import Card from "../Cards/Card";

const Details: React.FC = () => {
  const { id } = useParams<{ id: string }>();

  const [request] = useState<IRequest>({ ...defaultRequest });

  const debugMode = useRef(false);

  const urlString = createlUrlFetch(id);

  const url = useRef(urlString);

  const isSingleRequest = useRef(true);

  const { response, isError, isLoading } = useFetch({ request, debugMode, url, isSingleRequest });

  return (
    <section className="details no-touch">
      {isLoading && <div className="hourglass" />}
      {isError && <p className="error-msg">Sorry, server hasn&apos;t answered. Try again later. </p>}
      {!isLoading && !isError && response.docs && response.docs.length && <Card data={response.docs[0]} />}
    </section>
  );
};

export default Details;
