import React from "react";

import { IRadioCall } from "../../../../../intefaces";

const InputRadio: React.FC<IRadioCall> = props => {
  const { radioData, callback } = props;

  const { name, labelText } = radioData;

  const handleClick = (e: React.ChangeEvent<HTMLInputElement>) => {
    callback.saveInput(e.target.value, e.target.name);
  };

  return (
    <>
      <label className="form__input-label" htmlFor={name + labelText}>
        {labelText}

        <input
          type="radio"
          className="form__input-radio"
          id={name + labelText}
          checked={labelText === callback.value[name]}
          value={labelText}
          name={name}
          onChange={handleClick}
        />
      </label>
    </>
  );
};

export default InputRadio;
